#main-nav li {
    list-style-type: none;
    display: block;
    text-align: center;
    padding: 5px;
    margin: 10px;
    border-radius: 10px;
    background-color: pink;
}
#main-nav a {
    text-decoration: none;
    color: white;
}
#arrow {
    width: 0;
    height: 0;
    border: solid 10px;
    border-color: black transparent transparent transparent;
}
.hide-small {
    display: none;
}

@media (min-width: 1100px) {
    #main-nav ul {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    #arrow {
        display: none;
    }

}

